import React from 'react';
import './PlaylistMain.css';
import PlaylistLeft from './PlaylistLeft.js'
import PlaylistRight from './PlaylistRight.js'
const BASE_URL = 'http://umang1126.herokuapp.com';




class PlaylistMain extends React.Component
{
	constructor(props)
	{
		super(props);
		this.state = {
			jsonArr: [],
			currentsong: '',
			currentanime: '',
			currentsinger: '',
			currentlyrics: [],
			yt_id: ''
		};
		this.onRemove = this.onRemove.bind(this);
		this.onTableClick = this.onTableClick.bind(this);
	}
	async onRemove(to_remove, e)
	{
		let url = BASE_URL + '/delete/' + to_remove;
		let response = await fetch(url);

		url = BASE_URL + '/get'
		response = await fetch(url);
		let jsonObj = await response.json();
		await this.setState({
			jsonArr: jsonObj
		})
	}
	async onTableClick(jsonObj, e)
	{
		let aname = jsonObj["ASLanime"];
		let sname = jsonObj["ASLsong"];
		let url = BASE_URL + '/anime/' + aname + '/song/' + sname;
		let response = await fetch(url)
		let data = await response.json();
		let lyrArr = data["lyrics"];
		this.setState({
			currentlyrics: lyrArr,
			currentsong: jsonObj["song"],
			currentanime: jsonObj["anime"],
			currentsinger: jsonObj["singer"],
			yt_id: jsonObj["yt_id"]
		})
	}
	async componentDidMount(){
		
		let url = BASE_URL + '/get'
		let response = await fetch(url);
		let jsonObj = await response.json();
		console.log(jsonObj);
		console.log(jsonObj.length);
		await this.setState({
			jsonArr: jsonObj
		})
	}


	render()
	{
		return(
			<div className = "playlist-main">
				<PlaylistLeft
				jsonArr = {this.state.jsonArr}
				currentsong = {this.state.currentsong}
				currentsinger = {this.state.currentsinger}
				currentanime = {this.state.currentanime}
				currentlyrics = {this.state.currentlyrics} 
				yt_id = {this.state.yt_id}
				onTableClick = {this.onTableClick}
				onRemove = {this.onRemove}
				/>
				
				<PlaylistRight
				currentsong = {this.state.currentsong}
				currentsinger = {this.state.currentsinger}
				currentanime = {this.state.currentanime}
				currentlyrics = {this.state.currentlyrics}
				/>
			</div>
		);
	}
}

export default PlaylistMain;