import React from 'react';
class PlaylistRight extends React.Component
{
	constructor(props)
	{
		super(props)
	}
	printLyrics()
	{
		let sl = [];
		let i = 0;
		for(;i<this.props.currentlyrics.length;i++)
		{
			sl.push(
				<div>
					{this.props.currentlyrics[i]}
				</div>
			);
		}
		return <div class = "lyrics">{sl}</div>
	}
	render()
	{
		return(
			<div class = "playlist-right">
				<div class= 'song-title2'>
					{this.props.currentsong}
				</div>

				<div class = "song-artist2">
					{this.props.currentsinger}
				</div>

				<div class = "song-anime2">
					{this.props.currentanime}
				</div>

				{this.printLyrics()}
			</div>
		);
	}
}

export default PlaylistRight