import React from 'react';

class Right extends React.Component
{
	constructor(props)
	{
		super(props);
	}
	printList(arrname, arrlink)
	{
		let sl = [];
		for(let i = 0; i < arrname.length;i++)
		{
			if(arrlink[i]==="#")
			{
				sl.push(
					<div key = {i} class = "song-list-head">
						{arrname[i]}
					</div>
				);
			}
			else
			{
				let str = "";
				if(i>this.props.OT)
					str = "";
				else if(i>this.props.ED)
					str = "ED " + (i-this.props.ED);
				else if(i>this.props.OP)
					str = "OP " + (i-this.props.OP);
				sl.push(
					<div class = "song-list-val" key = {i} onClick = {(e) =>(this.props.handleSongClick(arrname,arrlink,i,str,e))}>
						{arrname[i]}
					</div>
				);
			}
		}

		return <div>{sl}</div>
	}
	render()
	{
		return(this.printList(this.props.songNameList, this.props.songLinkList));
	}
}


export {Right}