import React from 'react';
import ReactPlayer from 'react-player'
class PlaylistLeft extends React.Component
{
	constructor(props)
	{
		super(props)
		this.displayList = this.displayList.bind(this);
	}
	displayList()
	{
		let i = 0;
		let sl = [];
		sl.push(
			<tr>
				<th> Serial No. </th>
				<th> Song </th>
				<th> Artist </th>
				<th> Anime </th>
				<th> Remove </th>
			</tr>
		)
		for(;i < this.props.jsonArr.length;i++)
		{
			let temp_song = this.props.jsonArr[i]["song"];
			let temp_singer = this.props.jsonArr[i]["singer"];
			let temp_anime = this.props.jsonArr[i]["anime"];
			let jsonObj = this.props.jsonArr[i];
			sl.push(
				<tr>
					<td>{i+1}</td>
					
					<td onClick = {(e) =>(this.props.onTableClick(jsonObj,e))}>
						{temp_song}
					</td>
					
					<td  onClick = {(e) =>(this.props.onTableClick(jsonObj,e))}>
						{temp_singer}
					</td>
					
					<td  onClick = {(e) =>(this.props.onTableClick(jsonObj,e))}>
						{temp_anime}
					</td>
					
					<td  onClick = {(e) =>(this.props.onTableClick(jsonObj,e))}>
						<button onClick = {(e) => (this.props.onRemove(temp_song,e))}>Remove</button>
					</td>
				</tr>
			);

		}
		return <table>{sl}</table>
	}
	render()
	{
		return(
			<div>
				<ReactPlayer
				url = {"https://www.youtube.com/watch?v=" + this.props.yt_id}
				/>
				{this.displayList()}
			</div> 
		);
	}
}

export default PlaylistLeft