import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App.js'
import PlaylistMain from './PlaylistMain.js'

class Page extends React.Component
{
	constructor(props)
	{
		super(props)

		this.state = {
			isApp:true
		}
		this.switch = this.switch.bind(this);
	}

	switch()
	{
		let ia = this.state.isApp;
		this.setState({
			isApp: !ia
		})
	}
	makeHeader()
	{
		return(
			<div className ="top">
				<span className = "header">
					Ririksu Chan
				</span>
				<button onClick = {this.switch}>
					Switch
				</button>
			</div>
			);
	}
	render()
	{
		let sl = [];
		if(this.state.isApp)
			sl = [this.makeHeader(), <App />];
		else
			sl = [this.makeHeader(), <PlaylistMain />];
		return(
			<div>
				<div className = "bg-image"></div>
				{sl}
			</div>
		);
	}
}

ReactDOM.render(
	<Page />,
	document.getElementById('root')
);
