import React from 'react';
import {Search, SearchList} from './Search.js'
import './App.css'
import {Right} from './Right.js'
import {Middle} from './Middle.js'

const BASE_URL = 'http://umang1126.herokuapp.com';
const SEARCH = BASE_URL + '/search/';
const ANIME = BASE_URL + '/anime/';
const YT_API = BASE_URL + '/yt/';



class App extends React.Component
{

	constructor(props)
	{
		super(props);
		this.state ={
			querySearch: '',
			queryApp: '',
			animeNameList: [],
			animeLinkList: [],
			currentanimename: '',
			currentanimelink: '',
			currentsonglink: '',
			song: '',
			singer: '',
			lyrics: [],
			songNameList: [],
			songLinkList: [],
			typeNum: '',
			OP:-1,
			ED:-1,
			OT:-1,
			yt_id:''
		};
		this.handleSearchChange = this.handleSearchChange.bind(this);
		this.handleSearchSubmit = this.handleSearchSubmit.bind(this);
		this.handleListClick = this.handleListClick.bind(this);
		this.animeNameExtract = this.animeNameExtract.bind(this);
		this.handleSongClick = this.handleSongClick.bind(this);
	}

	async handleSongClick(arr1,arr2,ind,str,e)
	{
		
		let song = arr1[ind];
		let currentsonglink = arr2[ind];
		let anpath = this.animeNameExtract(this.state.currentanimelink);
		let spath = this.animeNameExtract(currentsonglink);
		let url = ANIME + anpath + '/song/' + spath;

		let response = await fetch(url);
		let data = await response.json();

		let singer = data["singer"];
		let lyricsArr = data["lyrics"];

		if(str === "")
			url = YT_API + song + ' ' + singer;
		else
			url = YT_API + this.state.currentanimename + str;
		response = await fetch(url);
		data = await response.text();

		await this.setState((state,props) =>({
			yt_id:data,
			singer: singer,
			lyrics: lyricsArr,
			song: song,
			currentsonglink: currentsonglink
		}))
	}
	animeNameExtract(url)
	{
		return (url.substring(url.lastIndexOf('/')+1));
	}
	async handleListClick(arr1,arr2,ind, e)
	{
		let currentanimename = arr1[ind];
		let currentanimelink = arr2[ind];

		let q = this.animeNameExtract(currentanimelink);

		let url = ANIME + q;

		let response = await fetch(url);
		let data = await response.json();
		let namearr = data["name"];
		let linkarr = data["link"];
		let Op = 0, Ed = 0, Ot = 0;
		let i = 0
		for(; i<namearr.length;i++)
		{
			if(linkarr[i]==="#")
			{
				let str = namearr[i];
				if(str.startsWith("Op"))
					Op = i;
				if(str.startsWith("En"))
					Ed = i;
				if(str.startsWith("Ot"))
					Ot = i;
			}
		}
		await this.setState((state,props)=>({
			OP:Op,
			ED:Ed,
			OT:Ot,
			songNameList: namearr,
			songLinkList: linkarr,
			currentanimename: arr1[ind],
			currentanimelink: arr2[ind]
		}))
	}

	handleSearchChange(e)
	{
		e.preventDefault();
		const val = e.target.value; //can replace with e.persist()
		this.setState((state,props) => ({
			querySearch: val
		}));
	}

	async handleSearchSubmit(e)
	{
		e.preventDefault();
		const sval = this.state.querySearch;
		const url = SEARCH + sval;
		let response = await fetch(url);
		let data = await response.json();
		let namearr = data["name"];
		let linkarr = data["link"];
		await this.setState((state,props) =>({
			queryApp: sval,
			animeNameList: namearr,
			animeLinkList: linkarr
		}))
	}
	render()
	{
		return (
			<div className = "app">
				<div className = "search">
					<Search
					query = {this.state.querySearch} 
					handleSearchChange = {this.handleSearchChange} 
					handleSearchSubmit = {this.handleSearchSubmit} />
					<SearchList
					query = {this.state.queryApp} 
					animeNameList = {this.state.animeNameList} 
					animeLinkList = {this.state.animeLinkList}
					handleListClick = {this.handleListClick}
					/>
				</div>

				<div className = "middle">
					<Middle
					lyrics = {this.state.lyrics}
					yt_id = {this.state.yt_id}
					currentanimename = {this.state.currentanimename}
					song = {this.state.song}
					singer = {this.state.singer}
					currentanimelink = {this.state.currentanimelink}
					currentsonglink = {this.state.currentsonglink}
					/>
				</div>

				<div className = "right">
					<Right 
					songNameList = {this.state.songNameList}
					songLinkList = {this.state.songLinkList}
					handleSongClick = {this.handleSongClick}
					OP = {this.state.OP}
					ED = {this.state.ED}
					OT = {this.state.OT}
					/>
				</div>
			</div>
		);
	}
}

export default App;