import React from 'react';
import ReactPlayer from 'react-player/youtube'

const BASE_URL = 'http://umang1126.herokuapp.com';

class Middle extends React.Component
{
	constructor(props)
	{
		super(props);
		this.addInfo = this.addInfo.bind(this);
	}
	animeNameExtract(url)
	{
		return (url.substring(url.lastIndexOf('/')+1));
	}
	printTitles()
	{
		return(
			<div>
				<div class = "song-title">
					{this.props.song}
				</div>
				<div class = "song-artist">
					{this.props.singer}
				</div>
				<div class = "song-anime">
					{this.props.currentanimename}
				</div>
			</div>
		);
	}
	async addInfo()
	{
		let url = ( BASE_URL+ '/add/'
			+ this.props.currentanimename + '/'
			+ this.props.song + '/'
			+ this.props.singer + '/'
			+ this.props.yt_id) + '/'
			+ this.animeNameExtract(this.props.currentanimelink) + '/'
			+ this.animeNameExtract(this.props.currentsonglink)

		let response = await fetch(url);

	}
	printAddButton()
	{
		if(this.props.lyrics.length > 1)
			return(
				<button onClick = {this.addInfo}>
					Add
				</button>
			);
	}
	printLyrics()
	{
		const lyr = this.props.lyrics.map(function(line){
			return(<div class = "line">{line}</div>)
		});

		return <div class = "lyrics">{lyr}</div>
	}
	render()
	{
		return(
			
			<div>
				<div class = "react-player">
				<ReactPlayer
				height = "315px"
				width = "560px"
				url = {"https://www.youtube.com/watch?v=" + this.props.yt_id} />
				</div>
				<div class ="not-player">
				{this.printTitles()}
				{this.printAddButton()}
				{this.printLyrics()}
				</div>
			</div>
			);
	}
}

export {Middle}